import styled from "styled-components";
import { Title3, Title4 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonMobile";
import { css } from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { useEffect, useRef, useState } from "react";
import { smooth_left } from "../../assets/styles/animations";

const SVerticalBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 5rem;

  box-sizing: border-box;
  width: 100%;
  max-width: 700px;
  min-height: 40rem;
  padding: 1.875rem 24px;

  @media (min-width: 600px) {
    padding: 1.875rem 4%;
  }
  text-align: left;

  border-radius: 1.063rem;
  background: ${({ color }) => {
    switch (color) {
      case "blue":
        return `
          radial-gradient(68.55% 82.85% at 11.32% 115.16%, #36DBFF 0%, rgba(54, 219, 255, 0) 100%), #2D85F7;),
          #2d84f7`;

      case "green":
        return `
          radial-gradient(68.55% 82.85% at 11.32% 115.16%,#48DAFC 0%, rgba(54, 219, 255, 0) 100%), 
          #10ACE0;`;

      case "pink":
        return `
          radial-gradient(51.72% 161.8% at 97.16% -41.48%,#682A77 0%, rgba(104, 42, 119, 0) 100%), 
          radial-gradient(54.77% 56.72% at 11.32% 115.16%, #B13F67 0%, rgba(177, 63, 105, 0) 100%), 
          #2B2257;`;

      case "darkblue":
        return `
          radial-gradient(101.6% 186.88% at 97.16% -41.48%,#0C0A5D 0%, rgba(13, 10, 94, 0) 100%), 
          radial-gradient(54.77% 56.72% at 11.32% 115.16%, #6C2EDA 0%, rgba(108, 46, 218, 0) 100%), 
          #050347;`;

      case "darkviolet":
        return `
          radial-gradient(48.5% 50.08% at 31.84% 78.44%, rgba(142, 71, 236, 0.2) 0%, rgba(0, 0, 0, 0) 100%, 
          rgba(142, 71, 236, 0) 100%), 
          radial-gradient(101.6% 186.88% at 97.16% -41.48%, #8E47EC 0%, rgba(13, 10, 94, 0) 100%), 
          radial-gradient(49.08% 61.64% at 1.78% 133.91%, #DFA25F 0%, rgba(223, 162, 95, 0) 100%), 
          #1F1F1F;`;
    }
  }};

  ${({ view }) => !view && smooth_left}
`;

const Desc = styled.div`
  max-width: 654px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.875rem;
`;

const Label = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
`;

const Name = styled(Title3)`
  font-weight: 400;
  font-size: 1.875rem;
  line-height: 110%;
`;

const textStyle = css`
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 150%;
`;

const Title = styled(Title4)`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 120%;
`;

const List = styled.ul`
  padding-left: 0.938rem;
  max-width: 547px;
  color: white;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;

  & li {
    position: relative;
    ${textStyle}
  }

  & li::before {
    content: "";
    background-color: #ffffff55; /* Цвет маркера */
    width: 7px;
    height: 7px;
    position: absolute;

    top: 0.313rem;
    left: -15px;
    border-radius: 100%;
  }
`;

const Socials = styled.div`
  display: flex;
  gap: 1.063rem;
  margin-top: 0.375rem;

  & svg {
    opacity: 0.5;
    width: 1.438rem;
    height: 1.438rem;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  gap: 0.625rem;

  & a {
    width: 100%;
    background-color: #ffffff;
    border-radius: 3.125rem;
  }
`;

const ProductView = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;

  & img {
    width: 100%;
    max-height: 499.24px;
  }
`;

function VerticalBox({
  boxcolor,
  btncolor,
  name,
  title,
  view,
  list,
  socials,
  href,
  language,
}) {
  const [view1, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  return (
    <SVerticalBox color={boxcolor} ref={elem} view={view1}>
      <Desc>
        <Label>
          <Name>{name}</Name>
          <Title>{title}</Title>
          <ProductView>
            <img loading="lazy" src={view} alt="Product View" />
          </ProductView>
          <List>
            {list &&
              list.map((list_item, index) => <li key={index}>{list_item}</li>)}
          </List>
        </Label>
        <Socials>{socials}</Socials>
        <ButtonBox>
          <a href={href} aria-label={"Узнать больше о продукте " + name}>
            <Button type="secondary" colorTxt={btncolor} link={true}>
              {language === "Ru" ? "Узнать больше" : EngLoc.buttons.more}
            </Button>
          </a>
        </ButtonBox>
      </Desc>
    </SVerticalBox>
  );
}

export default VerticalBox;
